import VueRouter from "vue-router";
import axios from 'axios'
import Error from "@/components/error/Error";
import UserManager from "@/components/sys/UserManager";
import RoleManager from "@/components/sys/RoleManager";
import PermissionManager from "@/components/sys/PermissionManager";
import UserInfoManage from "@/components/manager/UserInfoManage";

axios.defaults.baseURL = "http://localhost:8081/"


let router = new VueRouter({
    mode: 'hash',
    routes: [
        {
            path: "/error",
            component: Error
        },
        {
            path: "/sys/user",
            component: UserManager,
        },
        {
            path: "/sys/role",
            component: RoleManager,

        },
        {
            path: "/sys/permission",
            component: PermissionManager,
        },
        {
            path: "/user/userinfo",
            component: UserInfoManage,
        },


    ]
})

